import "../css/index.scss";
import { DropZone } from "./lib/DropZone";
(function ($) {
    $.fn.dropZone = function (options) {
        var settings = $.extend({
            debug: false,
            testing: false,
            multiple: false,
        }, options);
        this.each(function () {
            var _this = this;
            new DropZone(this, settings, function (files) {
                _this.trigger("dropzone.drop", files);
            });
        });
        return this;
    };
})(jQuery);
